import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CSRFToken from '../../components/CSRFToken'
import { connect } from 'react-redux';
import { updateError } from '../../actions/auth';

class Register extends Component {

    componentWillUnmount = () => {
        const { updateError } = this.props;
        updateError({});
    }

    render() { 
        const { error } = this.props;
        console.log('username' in error)
        return (
            <div className="container col-md-6">
                <h3 className='text-center'>Register</h3>
                <form onSubmit={this.props.onSubmit}>
                    <CSRFToken />
                    <div className="mb-3">
                        <label htmlFor="inputUsername" className="form-label">Username  { 'username' in error ? <span className='text-danger'>{error.error}</span> : ''}</label>
                        <input onChange={this.props.onChange} name='username' type="text" className="form-control" id="inputUsername" aria-describedby="usernameHelp" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputEmail" className="form-label">Email</label>
                        <input onChange={this.props.onChange} name='email' type="email" className="form-control" id="inputEmail" aria-describedby="emailHelp" />
                        <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputPassword" className="form-label">Password  { 'password' in error ? <span className='text-danger'>{error.error}</span> : ''}</label>
                        <input onChange={this.props.onChange} name='password' type="password" className="form-control" id="inputPassword" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputPassword2" className="form-label">Confirm Password  { 'password2' in error ? <span className='text-danger'>{error.error}</span> : ''}</label>
                        <input onChange={this.props.onChange} name='password2' type="password" className="form-control" id="inputPassword2" />
                    </div>
                    <button type="submit" className="btn btn-primary">Sign Up</button>
                </form>
                <p className='mt-3'>Already have an account? <Link to="/auth/login">Log in</Link></p>
            </div>
        );
    }
}
 
const mapStateToProps = state => ({
    error: state.auth.error
})
 
export default connect(mapStateToProps, { updateError })(Register);
