import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CSRFToken from '../../components/CSRFToken'
import { connect } from 'react-redux';
import { updateError } from '../../actions/auth';

class Login extends Component {
    state = {  } 

    componentWillUnmount = () => {
        const { updateError } = this.props;
        updateError({});
    }

    render() { 
        const {error} = this.props;
        return (
            <div className="container col-md-6">
                <h3 className='text-center'>Login</h3>
                <form onSubmit={this.props.onSubmit}>
                    <CSRFToken />
                    <p className='text-danger'>{error.error}</p>
                    <div className="mb-3">
                        <label htmlFor="inputUsername" className="form-label">Username</label>
                        <input onChange={this.props.onChange} name='username' type="text" className="form-control" id="inputUsername" aria-describedby="usernameHelp" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputPassword" className="form-label">Password</label>
                        <input onChange={this.props.onChange} name='password' type="password" className="form-control" id="inputPassword" />
                    </div>
                    <button type="submit" className="btn btn-primary">Log In</button>
                </form>
                <p className='mt-3'>Don't have an account? <Link to="/auth/register">Sign Up</Link></p>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    error: state.auth.error
})
 
export default connect(mapStateToProps, { updateError })(Login);